body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 20px;
}


.addNewLog {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 20px;
    position: relative;
    background-color: #f9f9f9;

}

.addNewLog input {
    height: 30px;
    padding: 5px;
    font-size: 16px;
}

.addNewLog textarea {
    height: 120px;
    padding: 5px;
    font-size: 16px;
}

.addNewLog button {
    width: 150px;
    padding: 10px;
    font-size: 16px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.addNewLog button div {
    margin: 0;
    margin-left: 5px;
  
}

.addNewLog button svg {
  
}

.addNewLog * {
    margin-bottom: 10px;
}
.confirmModal {
    display: none;
    position: absolute;
    top: 45%;
    left: 50%;
    background-color: grey;
    padding: 10px;
}
.log {
    position: relative;
}

.log-container {
    display: grid;
    grid-template-areas:
    "content delete";
    grid-template-columns: 1fr 100px;
    padding: 20px;
}

.content {
    grid-area: content;
}

.delete {
    grid-area: delete;
    height: 20px;
    margin: 40px 20px 0 0;
}
